/* ------------------- */
/* Custom properties   */
/* ------------------- */

:root {
    /* colors */
    --clr-main: #F9C933;
    
    /* font-sizes */
    --fs-600: 1.4rem;
    --fs-500: 1.1rem;
    --fs-400: 0.9375rem;
    --fs-300: 8rem;
    --fs-200: 0.7rem;
    
    /* font-families */
    --ff-sans: "Poppins", serif;
}

/* ------------------------------------ */
/* ------------- Utilities ------------ */
/* ------------------------------------ */

.fixed-position {
    overflow: hidden;
 }

 .txt-yellow {
    color: var(--clr-main);
 }

.flex {
    display: flex;
}

.flex-col {
    display: flex;
    flex-direction: column;
}

 /* ------------------------------------ */
/* ------------- Reset ----------------- */
/* ------------------------------------ */

html {
    font-size: 92%; 
  }

@media (min-width: 600px) {
    html {
        font-size: 95%; 
      }
}

@media (min-width: 9000px) {
    html {
        font-size: 100%; 
      }
}

*,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'poppins', sans-serif !important;
    font-weight: 400;
}

body {
    overflow-x: hidden;
}
    
img {
    display: block;
    max-width: 100%;
}

ul {
    list-style-type: none;
}

a {
    display: block;
    text-decoration: none;
}