:root {
    --btn-width: 24px;
    --btn-height: calc(var(--btn-width) * .81);
    --rectangle-height: calc(var(--btn-width) * .1);
    --rectangle-radius: calc(var(--btn-width) * .04);
    --translate: calc(var(--btn-width) * .17);
    --transition: 0.3s;
}

.header-container {
    background: #000000;
    color: #ffffff;
    width: 100%;
}

.header {
    width: 93%;
    max-width: 1200px;
    padding-block: .75em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-inline: auto;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar ul {
    display: flex;
    align-items: flex-end;
    gap: 2em;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
}

.navbar li span {
    color: var(--clr-main);
    font-size: .5rem;
}

.navbar a {
    color: #ffffff;
    font-size: .8rem;
    font-weight: 500;
    margin-top: -5px;
}

.profileCTA-container {
    font-size: .8rem;
    gap: .5em;
    align-items: center;
}

.profileCTA-container p {
    font-weight: 500;
}


@media (max-width: 1050px) {

    .navbar {
        background: #000000;
        position: fixed;
        inset: 0 0 0 0;
        z-index: 10000;

        flex-direction: column;
        align-items: center;
        padding-top: min(25vh, 10em);

        transform: translateX(100%);
        transition: transform 400ms ease-in-out;
    }

    .navbar a {
        font-size: 1rem;
    }

    .profileCTA-container p {
        font-size: .9rem;
    }

    .profileCTA-container {
        cursor: pointer;
    }
    
    .navbar ul {
        flex-direction: column;
        align-items: flex-start;
        gap: 1.2em;
    }

    .profileCTA-container {
        position: absolute;
        top: 15px;
        left: 20px;
    }
}

@media (min-width: 1050px) {
    .navbar ul {
        min-width: 600px;
    }
}
