:root {
    --btn-width: 19px;
    --btn-height: calc(var(--btn-width) * .75);
    --rectangle-height: calc(var(--btn-width) * .12);
    --rectangle-radius: calc(var(--btn-width) * .05);
    --translate: calc(var(--btn-width) * .17);
    --transition: 0.3s;
}

.menu-btn__container {
    z-index: 100000;
    padding: .5em .4em;
    border-radius: 6px;
    background-color: var(--clr-main);
}

.menu-btn {
    display: block;
    position: relative;
    width: var(--btn-width);
    height: var(--btn-height);
    border: 0;
    background: transparent;
    outline: none;
    cursor: pointer;
}

.menu-btn span {
    position: absolute;
    height: var(--rectangle-height);
    background: #000;
    border-radius: var(--rectangle-radius);
    transition: transform var(--transition);
}

.rectangle-small {
    width: calc(var(--btn-width) / 2);
}

.rectangle-top {
    top: 0;
    left: 0;
    transform-origin: top left;
}

.rectangle-middle {
    top: 50%;
    left: 0;
    width: var(--btn-width);
    transform: translateY(-50%);
}

.rectangle-bottom {
    bottom: 0;
    right: 0;
    transform-origin: bottom right;
}

.rectangle-top.open {
    transform: translateX(var(--translate)) rotate(45deg);
}

.rectangle-middle.open {
    transform: translateY(-50%) rotate(-45deg);
}

.rectangle-bottom.open {
    transform: translateX(calc(var(--translate) * -1)) rotate(45deg);
}

.navbar.open {
    transform: translateX(0);
}

@media (min-width: 1050px) {
    .menu-btn__container {
        display: none;
    }
}